@import '../../global.scss';

.NavbarSup {
    background: linear-gradient(to left top, #fff, $main-color);
    width: 100%;
    padding: 0.5rem 0;
    background-color: #1C3643;
    position: relative;
  }
  
  .NavbarSup__brand {
    color: #ffffff;
    display: flex;
    align-items: center;
    font-family: $logo-font;
    font-size: 2.4em;
    letter-spacing: 1.5px;
    width: fit-content;
  }
  .NavbarSup__brand span:nth-of-type(2) {
    margin: 5.5px;
    letter-spacing: 2.5px;
    
  }
  
  .NavbarSup__brand:hover {
    text-decoration: none;
    color: white
  }
  
  .NavbarSup__brand-logo {
    margin-right: 1.25rem;
    width: 76px;
    border-radius: 50%;
  }
  .NavbarSup__logIn {
    background: transparent;
    border: transparent;
    position: absolute;
    right: 12px;
    top: 24px;
    color: white;
    font-size: 1.25em;
    font-weight: 600;
    font-family: 'Times New Roman', Times, serif;
    padding: 0;
  }
  .NavbarSup__logIn:hover {
    color: $main-color;
    text-shadow: 2px 2px 2px 2px black;
  }
  .NavbarSup__logIn:active {
    background: transparent;
    border: transparent;
  }
@media screen and (max-width: 1023px) {
  .NavbarSup__brand {
    font-size: 2.2em;
  }
  .NavbarSup__brand-logo {
    width: 70px;
  }
}
@media screen and (max-width: 767px) {
  .NavbarSup__brand {
    font-size: 2em;
  }
  .NavbarSup__brand-logo {
    width: 65px;
  }
  .NavbarSup__logIn {
    font-size: 1.15em;
  }
}
@media screen and (max-width: 639px) {
  .NavbarSup__brand {
    font-size: 1.75em;
  }
  .NavbarSup__brand-logo {
    width: 60px;
  }
  .NavbarSup__logIn {
    font-size: 1em;
  }
}
@media screen and (max-width: 519px) {
  .NavbarSup {
    padding: 0.4rem 0;
  }
  .NavbarSup__brand {
    font-size: 1.6em;
  }
  .NavbarSup__brand-logo {
    width: 55px;
  }
  .NavbarSup__logIn {
    font-size: .85em;
  }
}
@media screen and (max-width: 479px) {
  .NavbarSup__brand {
    margin: auto;
  }
  .NavbarSup__logIn {
    display: none;
  }
}
@media screen and (max-width: 439px) {
  .NavbarSup__brand {
    font-size: 1.5em;
  }
  .NavbarSup__brand-logo {
    width: 52px;
    margin-right: 12px;
  }

}
@media screen and (max-width: 389px) {
  .NavbarSup {
    padding: 0.32rem 0;
  }
  .NavbarSup__brand {
    margin-right: 0;
    font-size: 1.4em;
  }
  .NavbarSup__brand-logo {
    width: 50px;
  }
}
@media screen and (max-width: 349px) {
  .NavbarSup__brand {
    font-size: 1.2em;
  }
}