.Mensaje {
    max-width: 720px;
    padding: 3.5em 4.5em;
    // background: url(https://www.pngkit.com/png/full/239-2392912_pergaminos-png-fondo-pergamino-para-word.png);
    // background: url(https://image.freepik.com/vector-gratis/bordes-transparentes-efectos-fondo-abstracto_91-2147487519.jpg);
    // background: url(https://w7.pngwing.com/pngs/513/98/png-transparent-parchment-background-angle-text-rectangle.png);
    background: url(https://previews.123rf.com/images/rustyphil/rustyphil1202/rustyphil120200026/12390140-grungy-marr%C3%B3n-amarillento-pergamino-vendimia-textura-de-papel.jpg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    position: relative;
    margin: auto;
}
.Mensaje_body {
    background: #ffffff4a;
    box-shadow: 0px 0px 20px 20px #ffffff4a;
}
    .Mensaje_fondo {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
    }
    .Mensaje_title {
        font-size: 1.5rem;
        text-align: center;
    }
    .Mensaje_date {
        font-size: .8em;
        color: #2e2e2e;
        position: absolute;
        right: 60px;
        top: 24px;
        text-align: center;
        font-weight: bold;
    }
    .Mensaje_text {
        margin: 0 1.25em;
        font-family: 'Helvetica Neue', Helvetica, sans-serif;
        text-align: center;
    }
    .Mensaje_author {
        position: absolute;
        right: 60px;
        bottom: 26px;
        font-weight: bold;
    }


@media screen and (max-width: 560px) {
    .Mensaje_date {
        right: 40px;
    }
    .Mensaje_author {
        right: 40px;
    }
}
@media screen and (max-width: 480px) {
    .Mensaje {
        padding: 3.5em 3.5em;
    }
}
@media screen and (max-width: 380px) {
    .Mensaje {
        padding: 3.25em 3.25em;
    }
    .Mensaje_date {
        right: 28px;
    }
    .Mensaje_author {
        right: 28px;
    }
}