.Modal {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    
  }
  
  .Modal__container {
    background-color: #ffffff;
    padding: 2.5em;
    border-radius: 7px;
    box-shadow: 0px 0px 20px 4px;
    position: relative;
  }
    .Modal_Xbutton {
      background-color: #137ea1c4;
      font-weight: bolder;
      font-size: 16px;
      position: absolute;
      top: 5px;
      right: 5px;
      padding: 4px 12px;
      border-radius: 10px;
      cursor: pointer;
    }