.Grado {
    display: flex;
    align-items: center;
    max-height: 400px;
    position: relative;
    width: 100%;
    margin: 1.75em 0;
    color: black;
}
.Grado:hover {
    box-shadow: 0px 0px 8px 0px;
    color: black;
}
    .Grado_image_Container {
        background: #80808059;
        margin: 0px;
        overflow: hidden;
        border-radius: 4px;
        margin-right: 0;
        margin-left: auto;
    }
    .Grado:nth-of-type(even) .Grado_image_Container {
        margin-right: auto;
        margin-left: 0;
    }
    .Grado img {
        max-height: 380px;
        object-fit: cover;
    }
    
    .Grado_details {
        left: 60px;
        right: initial;
        position: absolute;
        background: #fff;
        padding: 20px;
        margin-top: auto;
        margin-bottom: auto;
        max-width: 300px;
        height: fit-content;
        text-align: center;
        border-radius: 100px;
        box-shadow: 0px 0px 20px 20px white;
    }
    .Grado:nth-of-type(even) .Grado_details{
        right: 60px;
        left: auto;
    }
        .Grado_title {
            font-size: 1.75rem;
        }
        .Grado_description {
        text-align: left;
        }
            .Grado_url {
            background: #fff;
            border: 1px solid;
            color: #056fff;
            padding: 6px 38px;
            border-radius: 5px;
            font-size: 1em;
            z-index: 4;
            }
            .Grado_url:hover {
            background: #bfcfd331;
            color: #0060e6;
            z-index: 4;
            }


@media screen and (max-width: 1199px) {
    .Grado_details {
        left: 20px;
        max-width: 285px;
        font-size: 15px;
    }
    .Grado:nth-of-type(even) .Grado_details{
        right: 20px;
    }
    .Grado img {
        max-height: 340px;
    }
}
@media screen and (max-width: 991px) {
    .Grado_image_Container {
        margin-right: -18px;
    }
    .Grado_details {
        left: -16px;
        max-width: 168px;
        padding: 10px 5px;
        box-shadow: 0px 0px 12px 18px white;
        border-radius: 0;
    }
    .Grado_title {
        font-size: 1.5rem;
    }
    .Grado:nth-of-type(even) .Grado_image_Container {
        margin-left: -18px;
    }
    .Grado:nth-of-type(even) .Grado_details {
        right: -16px;
    }
    .Grado img {
        max-height: 312px;
    }
}
@media screen and (max-width: 767px) {
    .Grado {
        margin-bottom: 7em;
    }
    .Grado_image_Container {
        margin-right: auto;
        margin-left: auto;
    }
    .Grado_details {
        left: 9%;
        top: 85%;
        max-width: 82%;
        padding: 0;
    }
    .Grado:nth-of-type(even) .Grado_image_Container {
        margin-left: 0;
    }
    .Grado:nth-of-type(even) .Grado_details {
        right: 9%;
    }
    .Grado:nth-of-type(4) {
        margin-bottom: 8em;
    }
    .Grado img {
        max-height: 287px;
    }
}
@media screen and (max-width: 599px) {
    .Grado:nth-of-type(1) {
        margin-top: .75em;
    }
    .Grado:nth-of-type(4) {
        margin-bottom: 9em;
    }
}
@media screen and (max-width: 515px) {
    .Grado:nth-of-type(3) {
        margin-bottom: 9em;
    }
    .Grado img {
        max-height: 256px;
    }
}
@media screen and (max-width: 479px) {
    .Grado {
        margin-bottom: 8.25em;
    }
    .Grado_url {
        padding: 5px 32px;
    }
    .Grado img {
        max-height: 232px;
    }
    .Grado:nth-of-type(3) {
        margin-bottom: 9.75em;
    }
    .Grado:nth-of-type(4) {
        margin-bottom: 10.5em;
    }
    .Grado_title {
        font-size: 1.35rem;
    }
}
@media screen and (max-width: 439px) {
    .Grado img {
        max-height: 220px;
    }
}
@media screen and (max-width: 420px) {
    .Grado {
        margin-bottom: 9.25em;
    }
    .Grado_title {
        font-size: 1.3rem;
    }
    .Grado_description {
        font-size: 14px;
    }
    .Grado_url {
        font-size: 14px;
        padding: 5px 30px;
    }
    .Grado img {
        max-height: 216px;
    }
}
@media screen and (max-width: 399px) {
    .Grado img {
        max-height: 200px;
    }
}
@media screen and (max-width: 379px) {
    .Grado img {
        max-height: 192px;
    }
    .Grado_title {
        font-size: 1.15rem;
    }
}
@media screen and (max-width: 365px) {
    .Grado img {
        max-height: 180px;
    }
}
@media screen and (max-width: 341px) {
    .Grado_title {
        font-size: 1.1rem;
    }
    .Grado img {
        max-height: 168px;
    }
}
