.Profesores_container {
    display: flex;
    flex-wrap: wrap;
  }
    .Profesor {
      cursor: pointer;
      border: 1px solid gray;
      border-radius: 10px;
      margin-bottom: 15px;
      margin: 10px;
      padding: 18px;
      background: #fff;
      box-sizing: content-box;
      width: auto;
      height: auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
      box-shadow: inset -4px -1px 11px 1px #9195993d;
    }
    .Profesor:hover {
      box-shadow: 0px 0px 12px 2px #35363652;;
    }    
      .Profesor_image_Container {
        width: auto;
        background: #80808059;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        overflow: hidden;
        border-radius: 50%;
        box-shadow: 9px 1px 17px 2px #7db3ff69;
      }
        .Profesor_image {
          object-fit: cover;
          top: 0;
          border-radius: 50%;
        }
      .Profesor_details {
        width: 48%;
        margin: 0 auto;
      }
        .Profesor_title {
            font-family: 'Fjalla One', sans-serif;
          font-size: 27px;
          margin: 0;
          margin-bottom: 10px;
          text-align: center;
        }
        .Profesor_curso {
          margin: 10px 0;
        }
        .Profesor_url {
          margin: 10px 0;
          font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
        }
        .Profesor_mensaje {
          font-size: 16px;
          font-family: -webkit-body;
        }
          .Profesor strong {
            font-family: 'Times New Roman', Times, serif;
            font-size: 1.2em;
            }



  @media screen and (max-width: 767px) {
    .Profesor_details {
      width: 86%;
    }
    .Profesor_image_Container {
      margin: 1em auto 1.1em;
    }
}
  @media screen and (max-width: 529px) {
    .Profesor_title {
      font-size: 24px;
    }
}
  @media screen and (max-width: 479px) {
    .Profesor_details {
      width: 88%;
    }
    .Profesor_image {
      width: 200px;
      height: 200px;
    }
}
  @media screen and (max-width: 479px) {
    .Profesor_title {
      font-size: 21px;
    }
}
  @media screen and (max-width: 379px) {
    .Profesor_title {
      font-size: 18px;
    }
    .Profesor_details {
      width: 92%;
    }
    .Profesor_image {
      width: 192px;
      height: 192px;
    }
    .Profesor_mensaje {
      font-size: 15px;
    }
}