@import '../../global.scss';

.Pregunta {
    padding: 1em;
    border-radius: 4px;
    font-family: 'Times New Roman', Times, serif;
    margin: 0 -.5em;
    padding-bottom: 0;
    margin-bottom: 1em;
}
    .Pregunta_title {
        font-weight: bold;
        margin-bottom: 6px;
    }
    .Pregunta_profe {
        font-size: 18px;
        margin-bottom: 10px;
    }
    .Pregunta_parrafo {
        font-size: 17px;
        text-align: justify;
    }
    .Pregunta_cover {
      height: 225px;
      display: block;
      margin: -.5em auto;
      background: grey;
      margin-bottom: 1em;
    }
    .Pregunta_CajaAlts {
        position: relative;
        display: flex;
        top: -5px;
        box-sizing: border-box;
        flex-wrap: wrap;
    }
      .Pregunta_alternativa {
        display: inline-block;
        position: relative;
        vertical-align: middle;
        padding-left: 20px;
        cursor: pointer;
      }
      .Pregunta_alternativa input[type="radio"] {
        display: none;
      }
      .Pregunta_alternativa span {
        width: 22px;
        height: 22px;
        border: 2.5px solid #fff;
        border-radius: 50%;
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        margin: 0 auto;
      }
      .Pregunta_alternativa span::after {
        content: "";
        height: 11px;
        width: 11px;
        background: #fff;
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%) scale(0);
        border-radius: 50%;
        transition: .25s ease-in-out 0s;
      }
      .Pregunta_alternativa input[type="radio"]:checked ~ span::after {
        transform: translate(-50%, -50%) scale(1);
      }
        .Pregunta_CajaAlts .Pregunta_alternativa {
            padding-left: 30px;
            font-size: 18px;
            line-height: 22px;
            width: -webkit-fill-available
        }
        .Pregunta_CajaAlts .Pregunta_alternativa span {
          border: 2.5px solid $main-color;
        }
        .Pregunta_CajaAlts .Pregunta_alternativa span::after {
          background: $main-color;
        }
  .Pregunta_Respuesta {
    margin-bottom: 0;
    text-align: center;
    font-size: 18px;
    border-radius: 2px;
    padding: 2px 0px;
    margin: 0 -16px;
  }

@media screen and (max-width: 767px) {
    .Pregunta_parrafo { font-size: 16px }
}
@media screen and (max-width: 519px) {
    .Pregunta_cover { height: auto; width: 100%; }
}
@media screen and (max-width: 479px) {
  .Pregunta_title { font-size: 1.35rem }
  .Pregunta_profe { font-size: 16px }
}