.Portada {
    position: relative;
    width: 100%;
    height: 400px;
}
    .Portada_Baner {
        background: transparent;
        width: 90%;
        height: inherit;
        padding: 0;
        overflow: hidden;
        position: relative;
        margin: 0 5%;
    }
        .Portada_Cambio {
            position: absolute;
            height: 100%;
            bottom: 0;
            width: 6.25%;
            display: flex;
            justify-content: center;
            align-items: center;
            background: rgba(255, 255, 255, 0.2);
            opacity: 0;
        }
        .Portada:hover .Portada_Cambio {
            opacity: 1;
            transition: 1s;
        }
        .der {
            right: 0;
        }
            .Portada_btn {
                font-size: 5.5em;
                font-family: 'Poppins', Helvetica, Arial,sans-serif;
                border: none;
                width: 100%;
                background: transparent;
                color: white;
            }
            .Portada_btn:focus {
                outline: none;
            }
        .Portada_Ruleta {
            position: relative;
            transition: .5s;
            height: inherit;
            left: 0%;
        }
        .Portada_Ruleta:hover {
            animation-play-state: paused;
        }
            .Portada_Ruleta-img {
                height: inherit;
                width: 100%;
                display: flex;
                background: #80808059;
                justify-content: center;
                align-items: center;
                position: absolute;
                flex-wrap: nowrap;
            }
            .Portada_Ruleta-img img {
                width: 100%;
            }
        .Portada_Posicionamiento {
            width: max-content;
            height: auto;
            position: absolute;
            display: flex;
            bottom: 10px;
            margin: auto;
            left: 0;
            right: 0;
        }
            .Portada_Posicionamiento-item {
                width: 20px;
                height: 20px;
                margin: 0 5px;
                background: rgba(#fff, $alpha: 0.25);
                border-radius: 50%;
            }
            #Portada-puntero {
                background: rgba(#fff, $alpha: 0.45);
                position: absolute;
                left: 0px;
                transition: .25s;
            }



// RESPONSIVE:
@media screen and (max-width: 1023px) {
    .Portada { height: 375px }
    .Portada_Cambio { opacity: 1 }
    .Portada_btn {font-size: 5em }
    .Portada_Baner { width: 95%;margin: 0 2.5% }
}
@media screen and (max-width: 767px) {
    .Portada { height: 310px}
    .Portada_Baner { width: 100%;margin: 0 }
    .Portada_Cambio { width: 6.5% }
    .Portada_btn {font-size: 4.5em }
}
@media screen and (max-width: 619px) {
    .Portada { height: 270px }
    .Portada_Cambio { width: 7.5% }
    .Portada_btn {font-size: 4.25em }
    .Portada_Posicionamiento-item {
        width: 18px;
        height: 18px;
        margin: 0 6px;
    }
}
@media screen and (max-width: 539px) {
    .Portada { height: 240px }
    .Portada_Cambio { width: 8.5% }
}
@media screen and (max-width: 479px) {
    .Portada { height: 230px }
    .Portada_btn {font-size: 4em }
    .Portada_Posicionamiento-item {
        width: 17px;
        height: 17px;
        margin: 0 6.5px;
    }
}
@media screen and (max-width: 439px) {
    .Portada { height: 220px }
    .Portada_btn {font-size: 3.5em }
}
@media screen and (max-width: 419px) {
    .Portada { height: 200px }
    .Portada_btn {font-size: 3.25em }
}
@media screen and (max-width: 379px) {
    .Portada { height: 162px }
    .Portada_Cambio { width: 9.5% }
    .Portada_btn {font-size: 2.75em }
    .Portada_Posicionamiento-item {
        width: 14px;
        height: 14px;
        margin: 0 8px;
    }
}