.Opinion {
    background-color: white;
    padding: 5px;
    border-radius: 4px;
    width: 96%;
    justify-self: center;
    position: relative;
}
    .Opinion_persona {
        display: inline-flex;
        align-items: center;
        margin-bottom: 4px;
    }
        .Opinion_persona-emoji {
            font-size: 30px;
            left: 16px;
            position: relative;
            text-shadow: 1px 1px 0px #ffc83d;
        }
        .Opinion_persona h6 {
            width: fit-content;
            font-size: 1.2em;
            margin: 0;
            margin-left: .5em;
        }
        .Opinion_persona-fecha {
            font-size: .8em;
            color: grey;
            position: absolute;
            right: 10px;
            top: 4px;
        }
        .Opinion_puntuacion {
            position: relative;
            left: 2px;
            top: 16px;
        }
            .Opinion_puntuacion i{
                color: red;
                font-size: 21px;
                text-shadow: 1px 1px 7px;
            }
            .Opinion_puntuacion span {
                font-family: "Font Awesome 5 Brands", "Font Awesome 5 Free";
                position: absolute;
                color: white;
                font-size: 14px;
                font-weight: 700;
                left: 7px;
            }
    .Opinion p {
        margin: 0 1.25em;
        font-family: 'Helvetica Neue', Helvetica, sans-serif;
    }

