@import '../../global.scss';

.Footer {
    color: #fff;
    display: flex;
    text-align: center;
    background: linear-gradient(to top, $main-color, #006688, $main-color);
    justify-content: center;
    padding: 4px 10px;
    flex-wrap: wrap;
  }  
    .Footer p {
      margin: 8px;
      font-family: 'Helvetica Neue', Helvetica, sans-serif;
    }
    .Footer a {
      color: #7ac7eb;
      text-decoration: none;
      cursor: pointer;
    }


@media screen and (max-width: 767px) {
    .Footer p {
      margin: 6px;
    }
}
@media screen and (max-width: 639px) {
    .Footer p {
      font-size: 15px;
    }
}
@media screen and (max-width: 539px) {
    .Footer p {
      font-size: 14.5px;
    }
}
@media screen and (max-width: 479px) {
  .Footer {
    padding: 0px 90px;
  }
    .Footer p {
      font-size: 14px;
    }
}
@media screen and (max-width: 442px) {
  .Footer {
    padding: 0px 62px;
  }
}
@media screen and (max-width: 386px) {
  .Footer {
    padding: 0px 33px;
  }
}
@media screen and (max-width: 328px) {
  .Footer {
    padding: 0px 10px;
  }
}