@import '../../global.scss';
.burger-button {
    display: none;
}
.NavbarInf {
    background: linear-gradient(to top, $main-color, #006688, $main-color);
}
    .NavbarInf_Menu{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        height: 60px;
        max-width: 90%;
        margin: 0 auto;
        padding: 0;
    }
        .NavbarInf_Menu-li {
            color: #fff;
            list-style-type: none;
            height: 100%;
            width: -webkit-fill-available;
        }
            .NavbarInf_Menu-li a {
                color: #fff;
                font-family: 'Gelasio';
                font-size: 1.3em;
                width: 100%;
                height: 100%;
                align-items: center;
                display: flex;
                justify-content: center;
            }
        .NavbarInf_Menu-li:hover {
            background: #3490ae;
        }
            .NavbarInf_Menu-li a:hover {
                text-decoration: none;
            }


@media screen and (max-width: 1023px) {
    .NavbarInf_Menu {
        height: 56px;
    }
}
@media screen and (max-width: 767px) {
    .NavbarInf_Menu {
        height: 52px;
    }
}
@media screen and (max-width: 639px) {
    .NavbarInf_Menu {
        font-size: 15px;
        height: 48px;
    }
}
@media screen and (max-width: 479px) {
    .burger-button {
        font-size: 20px;
        width: 40px;
        height: 40px;
        line-height: 40px;
        border-radius: 50%;
        background-color: rgba(0, 0, 0, 0.8);
        display: block;
        text-align: center;
        position: fixed;
        z-index: 2;
        left: 6px;
        top: 12px;
        color: white;
    }
    .NavbarInf_Menu {
        position: fixed;
        max-width: 100%;
        background: rgba(14, 120, 155, 0.86);
        z-index: 1;
        top: 0;
        left: -100%;
        margin: 0;
        width: 100%;
        height: 100vh;
        display: inline-block;
        transition: .3s;
    }

    .visible {
        left: 0;
    }
    .NavbarInf_Menu-li {
        height: 2em;
        margin: 1.75em 0;
        width: 100%;
        display: flex;
        justify-content: center;
        font-size: 1.7em;
    }
    .NavbarInf_Menu-li:hover {
        background: transparent;
    }
    .NavbarInf_Menu-li a {
        width: fit-content;
    }
}
@media screen and (max-width: 411px) {
    .burger-button {
        width: 36px;
        height: 36px;
        line-height: 36px;
        left: 4px;
        top: 4px;
        font-size: 18px;
    }
}
@media screen and (max-width: 380px) {
    .NavbarInf_Menu-li {
        font-size: 1.6em;
    }
}