@import url('https://fonts.googleapis.com/css?family=Fjalla+One|Roboto|Poppins|Libre+Franklin:300i|Source+Sans+Pro|Girassol|Oswald|Gelasio|Rakkas|Lobster|Trade+Winds&display=swap');

$logo-font: 'Lobster', sans-Serif;
$main-color: #137ea1;
// $main-color: #8f57fd;

body {
    margin: 0;
    background: #f6f6f6;
  }
  
.sub-titulo {
  text-transform: uppercase;
  margin: .75em 0;
  font-size: 1.85em;
}
.btn {
  padding: 0.375rem 2rem;
  font-size: 20px;
}

.btn.btn-primary {
  color: #fff;
  background-color: $main-color;
  border-color: $main-color;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  box-shadow: 0px 0px 10px 0px $main-color;
  background-color: $main-color;
  border-color: $main-color;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: $main-color;
  border-color: $main-color;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(32, 99, 221, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}

// h3 {
//   font-size: 1.85rem;
//   word-spacing: 7px;
// }
.cajita {
  justify-items: center;
  justify-content: center;
  display: grid;
}
.gridCols {
  display: grid;
  grid-template-columns: repeat(2, minmax(auto, 550px));
  grid-column-gap: 0;
  grid-row-gap: 10px;
  justify-content: center;
  margin-top: 10px;
  margin-bottom: 12px;
}
@media screen and (max-width: 1023px) {
  .sub-titulo {
    font-size: 1.75em;
    margin: .6em 0;
  }
  .gridCols {
    grid-template-columns: auto;
  }
}
@media screen and (max-width: 767px) {
  .sub-titulo {
    font-size: 1.5em;
  }
  h4 {
    font-size: 1.25em;
    margin: .25em .85em;
  }
}
@media screen and (max-width: 599px) {
  .sub-titulo {
    font-size: 1.35em;
    margin-top: .75em;
  }
}
@media screen and (max-width: 479px) {
  .sub-titulo {
    font-size: 1.25em;
    margin-top: .5em;
  }
  .btn {
    padding: 0.3rem 1.25rem;
    font-size: 18px;
  }
}
@media screen and (max-width: 379px) {
  .sub-titulo {
    font-size: 1.1em;
  }
}