.GradoPage_fila {
    margin: 0 1.75em;
    box-shadow: 0px 0px 16px 20px white;
    padding: .75em 2em;
    background-color: white;
}
.GradoPage_fila:nth-of-type(4) h3 {
    text-align: center;
}
.GradoPage_fila:nth-of-type(4) h3 span {
    font-weight: 400;
    font-size: .9em;
}
.GradoPage_title {
    text-align: center;
    margin: .5em 0;
    font-size: 2.25em;
}
.GradoPage_mensaje {
    text-align: center;
    margin: 0 6em;
    font-size: 17px;
    font-family: 'Trade+Winds';
}
.GradoPage_lista {
    font-size: 17px;
    margin: .5em 0;
}
.GradoPage_score {
    font-family: 'Times New Roman', Times, serif;
    font-size: 2em;
    font-weight: bold;
}
.mayus {
    list-style-type: none;
    text-transform: uppercase;
}
.puntaje {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: -8px;
    padding-top: 0;
    text-align: center;
}


@media screen and (max-width: 1023px) {
    .GradoPage_mensaje { margin: 0 2.5em }
}
@media screen and (max-width: 767px) {
    .GradoPage_mensaje { margin: 0 }
    .GradoPage_fila { margin: 0 .5em }
    .puntaje { margin-top: -8px }
    .GradoPage_mensaje { font-size: 16px }
    .GradoPage_lista { font-size: 16px }
    .GradoPage_fila h3 { font-size: 1.65em }
}
@media screen and (max-width: 575px) {
    .GradoPage_fila { margin: 0 }
    .puntaje { margin-top: -8px }
    .GradoPage_mensaje { margin: 0 -1em }
    .GradoPage_lista { font-size: 15px }
}
@media screen and (max-width: 479px) {
    .GradoPage_title { font-size: 2em }
    .GradoPage_mensaje { font-size: 15px }
    .GradoPage_fila { padding: .75em 1em }
    .puntaje { padding-top: 0 }
    .GradoPage_fila h3 { font-size: 1.5em }
    .GradoPage_lista { font-size: 14.5px }
    .GradoPage_score { font-size: 1.85em }
}
@media screen and (max-width: 419px) {
    .GradoPage_lista span {
        display: block;
        margin-bottom: .5em;
        margin-left: 1.25em;
     }
     .GradoPage_score { font-size: 1.75em }
     .puntaje { padding: .75em .5em }
}
