.NotFound_text {
    grid-template-rows: auto auto;
    margin: 1.5em 0;
}
.NotFound_text h1 {
    font-size: 5em;
}
.NotFound_image_cover {
    max-width: 640px;
    width: 88%;
}
.NotFound_return {
    padding-bottom: 1em;
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 540px) {
    .NotFound_text h1 {
        font-size: 4em;
    }
}
@media screen and (max-width: 440px) {
    .NotFound_text h2 {
        font-size: 1.5em;
    }
}