.Contacto {
    background-color: #ebebeb;
    height: auto;
    color: #000;
    align-items: center;
    padding-bottom: 1em;
    flex-wrap: wrap;
    padding-top: 0.01em;
  }
    .Contacto_contenido {
      display: flex;
      justify-content: space-around;
    }
        .Contacto_datos {
            padding: 0;
            margin-right: 1em;
        }
            .Contacto_image {
            width: 180px;
            height: 180px;
            overflow: hidden;
            margin-right: 1.5em;
            float: left;
            object-fit: cover;
            border-radius: 4px;
            border: 1px solid #1b2127;
            }
            .Contacto_datos h4 {
                font-family: 'Oswald', Helvetica, sans-serif;
                font-size: 28px;
                margin-bottom: 36px;
            }
            .Contacto_datos p {
                font-family: 'Libre Franklin';
                font-size: 17px;
            }
        .Contacto_ubicacion {
            text-align: center;
            height: 400px;
            box-shadow: 0px 2px 8px 0px black;
        }
            .Contacto_mapa {
                border: 0;
                width: 100%;
                height: 100%;
            }
        .fb-acad-logo {
            display: flex;
            justify-content: center;
            align-items: center;
        }
            .fb-acad-logo a{
                font-size: 36px;
            }
            .fb-acad-logo a:hover{
                text-decoration: none;
            }
@media screen and (max-width: 991px) {
    .Contacto_ubicacion {
        height: 340px;
    }
    .Contacto_datos p {
        font-family: 'Libre Franklin';
        font-size: 16px;
    }
    .Contacto_image {
        width: 165px;
        height: 160px;
        margin-right: 1.25em;
    }
    .fb-acad-logo {
        margin-top: -16px;
    }
}
@media screen and (max-width: 767px) {
    .Contacto .col-6 {
        flex: initial;
        max-width: initial;
    }
    .fb-acad-logo {
        margin-top: -24px;
        margin-bottom: 1.5em;
    }
    .Contacto_datos {
        margin-right: 0em;
    }
    .Contacto_ubicacion {
        height: 280px;
    }
}
@media screen and (max-width: 480px) {
    .Contacto_ubicacion {
        height: 260px;
    }
}
@media screen and (max-width: 479px) {
    .Contacto_datos h4 {
        font-size: 24px;
        margin-bottom: 28px;
    }
    .Contacto_datos p {
        font-size: 15px;
    }
    .fb-acad-logo {
        margin-top: -12px;
    }
    .fb-acad-logo a{
        font-size: 28px;
    }
    .Contacto_ubicacion {
        height: 238px;
    }
}
@media screen and (max-width: 421px) {
    .Contacto_datos h4 {
        margin-bottom: 12px;
    }
    .fb-acad-logo {
        margin-top: 0;
    }
    .fb-acad-logo a{
        font-size: 26px;
    }
}
@media screen and (max-width: 408px) {
    .Contacto_datos h4 {
        margin-bottom: 88px;
    }
    .fb-acad-logo {
        margin-top: -68px;
    }
}

@media screen and (max-width: 344px) {
    .Contacto_image {
        width: 150px;
        height: 150px;
        margin-right: 1em;
    }
    .Contacto_datos h4 {
        margin-bottom: 78px;
    }
    .Contacto_ubicacion {
        height: 220px;
    }
}
@media screen and (max-width: 325px) {
    .Contacto_image {
        width: 144px;
        height: 144px;
    }
    .Contacto_datos h4 {
        margin-bottom: 68px;
    }
    .fb-acad-logo {
        margin-top: -56px;
    }
    .fb-acad-logo a{
        font-size: 24px;
    }
}